/* Home */
.windows-container {
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100vw;
  height: 200px;
}

.window-archive img, 
.window-new img {
  width: 80px;
  height: 80px;
}

.window-archive {
  background-image: url(/public/img/archive/3.webp);
}

.window-new {
  background-image: url(/public/img/new/1.webp);
}

.window-archive,
.window-new {
  margin: 15px;
  width: 100vw;
  background-repeat: no-repeat;
  background-position: top;
  background-size: cover;
  box-shadow: 0 0 10px #dadada;
  border: 2px solid #8ad0ff;
  cursor: pointer;
}

/* Pages */
.back-button {
  position: fixed;
}

.pages-mavlid img {
  width: 100vw;
  height: auto;
}

.back-button {
  position: fixed;
  bottom: 5px;
  left: 5px;
  padding: 10px 20px;
  font-size: 16px;
  color: white;
  background: rgba(0, 87, 179, 0.644);
  border: none;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 123, 255, 0.3);
  cursor: pointer;
  z-index: 10;
}

.share-button {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  background: none;
  border: none;
  cursor: pointer;
  background-color: #64bbf5;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  padding: 10px;
  border-radius: 10px;
}

.share-icon {
  width: 30px;
  height: 30px;
}

